import { createStore, compose, applyMiddleware } from "redux";

import thunk from "redux-thunk";
//cc reducer
import rootReducer from "./../reducer/index";

//process bin ca node js  kim tra mi trng
//mi trng dev mi dng dev tool nu up ln host ri th ko cn
const composeEnhancers =
  //kim tra xem c phi nh sn xut hay l pht trin
  process.env.NODE_ENV !== "production" &&
  //bin global window c phi object hay ko th mi xi extension dev tool
  typeof window === "object" &&
  window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
    ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({
        shouldHotReload: false
      })
    : compose;

const configureStore = () => {
  //middleWare cha cc danh sch cc middleWera
  let middleWare = [thunk]; // trc trong mng th u tin hn
  //truyn cc middleWera vo
  const enhancers = [applyMiddleware(...middleWare)];
  const store = createStore(rootReducer, composeEnhancers(...enhancers));
  return store;
};

export default configureStore;
