import React, { Component } from "react";

import { Route } from "react-router-dom";
class LoginLayoutRoute extends Component {
  render() {
    //cch 1 vit thng thng
    // const {component,exact,name,path} = this.props;
    //cch 2 dng ES6
    //...remainProps l cc props cn li
    const { component: YourComponent, ...remainProps } = this.props;
    return (
      <Route
        {...remainProps}
        //render Truyn vo mt function, function ny s tr v React Component.
        //N cng s c gi khi tr ng ng dn
        render={routeProps => {
          return <YourComponent {...routeProps} />;
        }}
      />
    );
  }
}

export default LoginLayoutRoute;
